import React, { useState, useEffect } from "react";
import { useNavigate } from 'react-router-dom'
import { OptionButton } from '../../../OtherPage/optionFunc/OptionButton'
import { TitleBar } from "../../../OtherPage/TitleBar/TitleBar";
import EditTournamentPopup from "./com/EditTournamentPopup/EditTournamentPopup";

import "./InputTournament.css"

//url
const backendUrl = require("../../../../DB/communication").backendUrl;


//true
const isEnpty = (strArray) => {
  let flag = false
  strArray.map((str) => {
    if (!str) {
      flag = true
    }
  })
  return flag
}

//true
const isDuplicate = (Array, key, id) => {
  let flag = false
  Array.map((u) => {
    if (u[id] === key) {
      flag = true
    }
  })
  console.log(key)
  return flag
}

//
const readTournament = (setTournamentData) => {
  fetch(backendUrl + "/tournament/tournament_call", {
    method: "POST",
    mode: "cors",
    headers: {
      "Content-Type": "application/json",
    },
  })
    .then((response) => response.json())
    .then((data) => setTournamentData(data))
}




//
const dateSplit = (nowdate) => {
  if (nowdate !== null) {
    let dateArray = nowdate.split('-');
    dateArray = { "year": dateArray[0], "month": dateArray[1], "day": dateArray[2] }
    return dateArray
  }

}





//
const makePulldown = (pulldownId, ArrayList, idText, nowSelected, setNowSelected) => {
  //pulldownId0
  //ArrayList
  //id
  //nowSelected[0]0
  //setNowSelectednowSeleceduseStatesetNowSelected()nowSelected

  return (
    <>
      <select id="tekitouni"
        onChange={(e) => {
          //
          //
          nowSelected = nowSelected.slice(0, nowSelected.length);
          nowSelected[pulldownId] = e.target.value
          setNowSelected(nowSelected)
          console.log(nowSelected)
        }
        }>
        {ArrayList.map((component, ind) => (
          <option value={ind}>{component[idText]}</option>
        ))
        }
      </select>
    </>
  )
}

//
const handleTournament = (
  setTournamentData,
  yearArray,
  monthArray,
  dayArray,
  nowOpeningDate,
  nowTournamentName,
  TournamentData
) => {
  console.log(
    yearArray[nowOpeningDate[0]].year + "-" +
    monthArray[nowOpeningDate[1]].month + "-" +
    dayArray[nowOpeningDate[2]].day
  )

  //
  if (TournamentData.some((v) => v.tournament_name === nowTournamentName)) {
    console.log("")
  }
  else if (nowTournamentName === "") {
    console.log("")
  }
  else {
    console.log(nowTournamentName + "")

    fetch(backendUrl + "/tournament/tournament_register", {
      method: "POST",
      mode: "cors",
      headers: {
        "Content-Type": "application/json",
      },
      //body: JSON.stringify({ email:login_id , password:login_ps }),
      body: JSON.stringify({
        "tournament_name": nowTournamentName,
        "opening":
          yearArray[nowOpeningDate[0]].year + "-" +
          monthArray[nowOpeningDate[1]].month + "-" +
          dayArray[nowOpeningDate[2]].day
      }),
    })
      .then(() => readTournament1(TournamentData, setTournamentData))
  }
}

//
const readTournament1 = (TournamentData, setTournamentData) => {
  fetch(
    backendUrl + "/tournament/tournament_call", {
    method: "POST", mode: "cors", headers: { "Content-Type": "application/json", },
  })
    .then((response) => response.json())
    .then((data) => {

      let count = 0
      data.map((u) => {
        if (TournamentData.some((v) => u.tournament_name === v.tournament_name)) count++

        console.log(TournamentData.some((v) => u.tournament_name === v.tournament_name))
      })
      if (count !== data.length) { setTournamentData(data); console.log("aaiiuu") }
    })
}

//
const editTournament = (urlTournamentId, newTournamentName, openingDate, TournamentData, setTournamentData) => {
  if (TournamentData.some((v) => v.tournament_name === newTournamentName)) {
    console.log("")
  }
  else {
    fetch(backendUrl + "/tournament/tournament_edit", {
      method: "POST",
      mode: "cors",
      headers: { "Content-Type": "application/json", },
      body: JSON.stringify({ tournament_id: urlTournamentId, tournament_name: newTournamentName, opening: openingDate }),
    })
      .then((response) => response.text())
      .then((data) => readTournament1([], setTournamentData))
  }
}

//
const tournamentDelete = (tournamentId, setTournamentData) => {
  fetch(backendUrl + "/tournament/tournament_edit", {
    method: "POST",
    mode: "cors",
    headers: { "Content-Type": "application/json", },
    body: JSON.stringify({ tournament_id: tournamentId }),
  })
    .then((response) => response.text())
    .then((data) => { if (data === "OK") { console.log(""); readTournament1([], setTournamentData) } })

  // console.log(tournamentId + "")
}




export const Input_Tournament = () => {

  //
  const initialYear = 2020
  const endYear = 2040
  const initialMonth = 1
  const endMonth = 12
  const initialday = 1
  const endDay = 31

  //
  let yearArray = []
  for (let i = initialYear; i <= endYear; i++) { yearArray = [...yearArray, { year: i }] }

  let monthArray = []
  for (let i = initialMonth; i <= endMonth; i++) { monthArray = [...monthArray, { month: i }] }

  let dayArray = []
  for (let i = initialday; i <= endDay; i++) { dayArray = [...dayArray, { day: i }] }

  //1:, 2:, 3:
  const [nowOpeningDate, setNowOpeningDate] = useState([0, 0, 0])

  //1:, 2:, 3:
  const [editOpeningDate, setEditOpeningDate] = useState([0, 0, 0])

  //
  const [nowTournamentName, setNowTournamentName] = useState("")

  //
  const [isDeleteMode, setIsDeleteMode] = useState(false);

  //
  const [editingTnmtName, setEditingTnmtName] = useState("")

  //,true,false
  const [EorDcheckBox, setEorDcheckBox] = useState(true)


  //
  let dateArray = { "year": "", "month": "", "day": "" }

  //
  let [TournamentData, setTournamentData] = useState([
    { "tournament_name": '31', "tournament_id": '55', "opening": "2022-03-01" },
    { "tournament_name": '9', "tournament_id": '56', "opening": "2022-02-01" },
    { "tournament_name": '5', "tournament_id": '57', "opening": "2022-01-01" },
    { "tournament_name": '22', "tournament_id": '58', "opening": "2021-08-01" }
  ])

  //
  const navigate = useNavigate()
  const PageTransition = (url) => {
    navigate(url)
  }



  useEffect(() => {
    readTournament1(TournamentData, setTournamentData)
    console.log("aa")
  }, [TournamentData])

  return (
    <div>
      <TitleBar
        TitleText={""}
        PageTransition={PageTransition}
        valueUrl={-1}
      />

      <OptionButton />


      <div class="headline"></div>
      <div class="whole1">
        
        <input type="text" onChange={(e) => { setNowTournamentName(e.target.value) }} />
        <br />
        
        <label>
          {makePulldown(0, yearArray, "year", nowOpeningDate, setNowOpeningDate)}
        </label>
        <label>
          {makePulldown(1, monthArray, "month", nowOpeningDate, setNowOpeningDate)}
        </label>
        <label>
          {makePulldown(2, dayArray, "day", nowOpeningDate, setNowOpeningDate)}
        </label>
        <br />

        {/*  */}
        {isEnpty([nowTournamentName]) &&
          <button
            class="btn_In_to"
            onClick={() => { }}>
          </button>
        }

        {!isEnpty([nowTournamentName]) &&
          <button
            class="btn_In_to"
            onClick={() => {
              handleTournament(
                setTournamentData, yearArray, monthArray, dayArray, nowOpeningDate, nowTournamentName, TournamentData
              )
            }}>
          </button>
        }

        {/*  */}
        <button
          onClick={() => { setIsDeleteMode(!isDeleteMode) }}
        >{isDeleteMode && ""}{!isDeleteMode && ""}</button>

      </div>

      <div class="headline"></div>
      <div class="whole">
        <div className="tournamentList">
          <div className="tournaments">

            {TournamentData.map((Tournament, ind) => {

              if (Tournament.tournament_name !== null) {
                //
                dateArray = dateSplit(Tournament.opening)

                return (
                  <div className="tournament">
                    <div className="days">
                      <span>{dateArray.year}{dateArray.month}{dateArray.day}</span>
                    </div>
                    <div className="tournamentName">
                      {isDeleteMode &&
                        <>
                          <EditTournamentPopup
                            sendClassName="btn_In_to1"
                            Tournament={Tournament}
                            ind={ind}
                            editTournament={editTournament}
                            editOpeningDate={editOpeningDate}
                            setEditOpeningDate={setEditOpeningDate}
                            yearArray={yearArray}
                            monthArray={monthArray}
                            dayArray={dayArray}
                            makePulldown={makePulldown}
                            TournamentData={TournamentData}
                            setTournamentData={setTournamentData}
                            editingTnmtName={editingTnmtName}
                            setEditingTnmtName={setEditingTnmtName}
                            EorDcheckBox={EorDcheckBox}
                            setEorDcheckBox={setEorDcheckBox}
                            tournamentDelete={tournamentDelete}
                            readTournament={readTournament}
                            dateSplit={dateSplit}
                            isDuplicate={isDuplicate}
                          />
                        </>

                      }
                      {!isDeleteMode &&
                        <button
                          className="btn_In_to1"
                          onClick={() =>
                            PageTransition(
                              "inputschool?urlTournamentId=" +
                              Tournament.tournament_id +
                              "&urlTournamentName=" +
                              Tournament.tournament_name
                            )
                          }>
                          {Tournament.tournament_name}
                        </button>
                      }
                      <br />
                    </div>
                  </div>
                )
              }

            })}
          </div>
        </div>
      </div>
    </div>
  )
}

export default Input_Tournament;