import './PullDown.css';
const { Position } = require("../../../../../../DB/Position")
const { ballPositionDB } = require("../../../../../../DB/ballPositionDB")


export const PullDown = (props) => {


  const setPosition = (text) => {
    props.setNowPosition(text)
  }

  //
  const setHit = (value) => {
    //
    props.setFlag(4)
    props.setcanvasX1(ballPositionDB[props.nowPosition - 1].ballPositionX)
    props.setcanvasY1(ballPositionDB[props.nowPosition - 1].ballPositionY)

    //
    props.setBatterResult(1)

    //
    let isTimelyText = "";
    if (props.addScoreState !== 0) isTimelyText = ""

    //
    if (value === 1) { props.setFreeWriteState(isTimelyText + Position[props.nowPosition - 1].kata + "") }
    else if (value === 2) { props.setFreeWriteState(isTimelyText + Position[props.nowPosition - 1].kata + ":") }
    else if (value === 3) { props.setFreeWriteState(isTimelyText + Position[props.nowPosition - 1].kata + ":") }
  }

  //
  const handleOut = (value) => {
    //
    props.setFlag(value)
    props.setcanvasX1(ballPositionDB[props.nowPosition - 1].ballPositionX)
    props.setcanvasY1(ballPositionDB[props.nowPosition - 1].ballPositionY)

    // //
    // props.setNowOutCountState(props.nowOutCountState + 1)

    //
    props.setBatterResult(0)

    //
    if (value === 1) { props.setFreeWriteState(":" + Position[props.nowPosition - 1].kata + "") }
    else if (value === 2) { props.setFreeWriteState(":" + Position[props.nowPosition - 1].kata + "") }
    else if (value === 3) { props.setFreeWriteState(":" + Position[props.nowPosition - 1].kata + "") }
  }

  //
  const handleError = (value) => {
    //
    props.setFlag(value)
    props.setcanvasX1(ballPositionDB[props.nowPosition - 1].ballPositionX)
    props.setcanvasY1(ballPositionDB[props.nowPosition - 1].ballPositionY)

    //
    props.setBatterResult(0)

    //
    if (value === 1) { props.setFreeWriteState(":" + Position[props.nowPosition - 1].kata + "") }
    else if (value === 2) { props.setFreeWriteState(":" + Position[props.nowPosition - 1].kata + "") }
    else if (value === 3) { props.setFreeWriteState(":" + Position[props.nowPosition - 1].kata + "") }
  }

  //
  const handleHomerun = (value) => {
    //
    let isTimelyText = "";
    if (props.addScoreState !== 0) isTimelyText = ""

    //
    props.setFlag(2)
    props.setcanvasX1(ballPositionDB[1 + value].ballPositionX)
    props.setcanvasY1(0)

    //
    props.setBatterResult(1)

    //
    if (value === 1) { 
      props.setFreeWriteState(isTimelyText + "") 
      props.setcanvasX1(ballPositionDB.find((v)=> v.name === "HR_left").ballPositionX)
      props.setcanvasY1(ballPositionDB.find((v)=> v.name === "HR_left").ballPositionY)
    }
    else if (value === 2) { 
      props.setFreeWriteState(isTimelyText + "") 
      props.setcanvasX1(ballPositionDB.find((v)=> v.name === "HR_center").ballPositionX)
      props.setcanvasY1(ballPositionDB.find((v)=> v.name === "HR_center").ballPositionY)
    }
    else if (value === 3) { 
      props.setFreeWriteState(isTimelyText + "") 
      props.setcanvasX1(ballPositionDB.find((v)=> v.name === "HR_right").ballPositionX)
      props.setcanvasY1(ballPositionDB.find((v)=> v.name === "HR_right").ballPositionY)
    }
    else if (value === 4) {
      props.setFlag(2)
      props.setFreeWriteState(isTimelyText + Position[props.nowPosition - 1].kata + "")
      props.setcanvasX1(ballPositionDB[props.nowPosition - 1].ballPositionX)
      props.setcanvasY1(ballPositionDB[props.nowPosition - 1].ballPositionY)
    }
  }

  //
  const handleBunt = (value) => {
    //
    props.setFlag(3)
    props.setcanvasX1(0)
    props.setcanvasY1(0)

    //
    props.setBatterResult(1)

    //
    if (value === 1) { props.setFreeWriteState("") }
    else if (value === 2) { props.setFreeWriteState("") }
    else if (value === 3) { props.setFreeWriteState("") }
    else if (value === 4) { props.setFreeWriteState("") }
  }


  //
  const handleSanshin = (value) => {
    //
    props.setFlag(0)
    props.setcanvasX1(0)
    props.setcanvasY1(0)

    //
    props.setBatterResult(0)

    //
    if (value === 1) { props.setFreeWriteState("") }
    else if (value === 2) { props.setFreeWriteState("") }
    else if (value === 3) { props.setFreeWriteState("") }
    else if (value === 4) { props.setFreeWriteState(":") }
  }


  //
  const handleShishikyu = (value) => {
    //
    props.setFlag(0)
    props.setcanvasX1(0)
    props.setcanvasY1(0)

    //
    if (value === 1) { props.setFreeWriteState(""); props.setBatterResult(2) }
    else if (value === 2) { props.setFreeWriteState(""); props.setBatterResult(3) }
  }



  return (
    <div className="PullDown">

      <ul id="dropmenu">

        <li><a href="#">{Position[props.nowPosition - 1].kata}</a>
          <ul>
            <li><a href="#" onClick={() => setPosition(1)}></a></li>
            <li><a href="#" onClick={() => setPosition(2)}></a></li>
            <li><a href="#" onClick={() => setPosition(3)}></a></li>
            <li><a href="#" onClick={() => setPosition(4)}></a></li>
            <li><a href="#" onClick={() => setPosition(5)}></a></li>
            <li><a href="#" onClick={() => setPosition(6)}></a></li>
            <li><a href="#" onClick={() => setPosition(7)}></a></li>
            <li><a href="#" onClick={() => setPosition(8)}></a></li>
            <li><a href="#" onClick={() => setPosition(9)}></a></li>
            <li><a href="#" onClick={() => setPosition(10)}></a></li>
            <li><a href="#" onClick={() => setPosition(11)}></a></li>
            <li><a href="#" onClick={() => setPosition(12)}></a></li>
            <li><a href="#" onClick={() => setPosition(13)}></a></li>
            <li><a href="#" onClick={() => setPosition(14)}></a></li>

          </ul>
        </li>
        <li><a href="#"></a>
          <ul>
            <li><a href="#"></a>
              <ul>
                <li><a href="#" onClick={() => setHit(1)}></a></li>
                <li><a href="#" onClick={() => setHit(2)}></a></li>
                <li><a href="#" onClick={() => setHit(3)}></a></li>
              </ul>
            </li>
            <li><a href="#"></a>
              <ul>
                <li><a href="#" onClick={() => handleOut(3)}></a></li>
                <li><a href="#" onClick={() => handleOut(2)}></a></li>
                <li><a href="#" onClick={() => handleOut(1)}></a></li>
              </ul>
            </li>
            <li><a href="#"></a>
              <ul>
                <li><a href="#" onClick={() => handleError(3)}></a></li>
                <li><a href="#" onClick={() => handleError(2)}></a></li>
                <li><a href="#" onClick={() => handleError(1)}></a></li>
              </ul>
            </li>
            <li><a href="#"></a>
              <ul>
                <li><a href="#" onClick={() => handleHomerun(1)}></a></li>
                <li><a href="#" onClick={() => handleHomerun(2)}></a></li>
                <li><a href="#" onClick={() => handleHomerun(3)}></a></li>
                <li><a href="#" onClick={() => handleHomerun(4)}></a></li>
              </ul>
            </li>
            <li><a href="#"></a>
              <ul>
                <li><a href="#" onClick={() => handleBunt(1)}></a></li>
                <li><a href="#" onClick={() => handleBunt(2)}>:</a></li>
                <li><a href="#" onClick={() => handleBunt(3)}></a></li>
                <li><a href="#" onClick={() => handleBunt(4)}></a></li>
              </ul>
            </li>
          </ul>
        </li>
        <li><a href="#">2</a>
          <ul>
            <li><a href="#"></a>
              <ul>
                {/*  */}
                <li><a href="#" onClick={() => handleSanshin(1)}></a></li>
                <li><a href="#" onClick={() => handleSanshin(2)}></a></li>
                <li><a href="#" onClick={() => handleSanshin(3)}></a></li>
                <li><a href="#" onClick={() => handleSanshin(4)}></a></li>
              </ul>
            </li>
            {/*  */}
            <li><a href="#" onClick={() => handleShishikyu(1)}></a></li>
            <li><a href="#" onClick={() => handleShishikyu(2)}></a></li>
            {/*  */}
            <props.GameEndPopup_field
            TableRegister = {props.TableRegister}
            urlGameId={props.urlGameId}
            TmpTableDelete={props.TmpTableDelete}
            DeleteDuringGame={props.DeleteDuringGame}
            CalculateBatAvg={props.CalculateBatAvg}
            schoolId1={props.schoolId1}
            schoolId2={props.schoolId2}
            />
            {/* <li><a href="#" onClick={() => handleGameEnd(props.GameEndPopup_field)}></a></li> */}
          </ul>
        </li>
      </ul>

    </div>
  );
}

export default PullDown;

